@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&family=Ubuntu+Mono&display=swap");
:root {
  --shell-topbar-blue: #090a11;
  --shell-body-blue: #121424;
  --blue: #121424;
  --lightblue: #0594e7;
  --green: #00ca4e;
}

::selection {
  color: #fff;
  background-color: var(--green);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

body {
  font-family: "Montserrat", sans-serif;
}

.navbar {
  background-color: var(--shell-body-blue);
  display: grid;
  flex-direction: column;
  vertical-align: text-bottom;
  align-items: center;
  justify-content: space-between;
  padding: 15px 50px;
  width: 20%;
  height: 100vh;
  top: 0;
  position: sticky;
}

.navbar-nav {
  display: flex;
  flex-direction: column;
}

.navbar-brand,
.nav-link {
  color: #fff;
  text-decoration: none;
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: 600;
}

.nav-item {
  list-style: none;
  display: block;
}

.nav-link {
  font-size: 1.1rem;
  padding: 10px;
}

.nav-link:hover {
  color: var(--green);
}

.container {
  display: grid;
  width: 80%;
  height: 100vh;
}

.shell-animation {
  background-color: var(--green);
  width: 100%;
  height: 100vh;
}

.shell {
  color: #fff;
  width: 75%;
  height: 80vh;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10vh;
  border-radius: 5px;
}

.shell-topbar {
  background-color: var(--shell-topbar-blue);
  width: 100%;
  margin-bottom: 0;
  padding: 10px;
  border-radius: 5px 5px 0 0;
}

.shell-icon {
  margin-right: 5px;
}

.shell-red {
  color: #ff3e3b;
}

.shell-yellow {
  color: #ffbd44;
}

.shell-green {
  color: #00ca4e;
}

.shell-body {
  background-color: var(--shell-body-blue);
  width: 100%;
  height: 70vh;
  margin-top: 0;
  padding: 50px;
  font-size: 1.3rem;
}

.text-blue {
  color: var(--lightblue);
}

.text-green {
  color: var(--green);
}

.slide-up {
  animation: slideUp 750ms;
}

@keyframes slideUp {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-1000px);
    display: none;
  }
}

.landing {
  color: var(--blue);
  background-color: var(--lightblue);
}

.landing-header {
  font-size: 1.2rem;
  font-weight: 600;
}
